class Board extends React.Component {
  rendername() {
    return <ShoppingList name={"aplication"} />;
  }
  rendername(price1, price2, price3) {
    return <ShoppingList price1={price1} price2={price2} price3={price3} />;
  }

  render() {
    return <div className="name">{this.rendername(200, 0, 200)}</div>;
  }
}

class Splist extends React.Component {
  render() {
    return <Board />;
  }
}

// ========================================

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<Splist />);
